<template>

    <div class="main-content">
        <div class="breadcrumbs" id="breadcrumbs">
            <ul class="breadcrumb">
                <li>
                    <i class="icon-home home-icon"></i>
                    <a href="#"></a>
                </li>
                <li class="active"><router-link to="/navs">微服务</router-link></li>
                <li class="active">办事指南</li>
                <li class="active">{{cur_subject_name}}</li>
            </ul>
        </div>

        <div id="page-content" class="page-content" :style="mianStyle">
            <div class="cs-box cs-wrap cs-bszn-topcate">
                <el-button v-for="item in subject_items" :plain="cur_subject_id != item.id" type="primary" @click="filterSubject(item)">{{item.name}}</el-button>
                <div class="cs-box cs-box-bszn-showcate" @click="showCatelist"><img src="../../img/icon-bszn-edit.png" class="cs-btn-edit-subject"></img><span class="css-bszn-rek">(办事指南类别)</span></div>
        </div>
            <div class="el-row contBox clear">
                <div :class="[onlyOneClass, is_show_update ? twoClass : '']">

                    <div class="cs-filter-box">
                        <!-- 筛选 -->
                        <div class="cs-export-box" v-if="false"><el-button :plain="true" type="primary" >导出</el-button></div>
                        <el-button-group>
                            <el-button :plain="flag != 6" type="primary" v-on:click="filterTag(6)">未发布</el-button>
                            <!--<el-button :plain="flag != 2" type="primary" v-on:click="filterTag(2)">未审批</el-button>-->
                            <el-button :plain="flag != 5" type="primary" v-on:click="filterTag(5)">已发布</el-button>
                            <!--<el-button :plain="flag != 4" type="primary" v-on:click="filterTag(4)">已撤销</el-button>-->
                        </el-button-group>
                        <div class="cs-box">
                            <div class="cs-searchBox" >
                                <input type="text" v-model="keyword" placeholder="事项名称" autocomplete="off" class="el-input__inner cs-search_input" @keyup.enter="sousuo" /><img v-on:click="sousuo" src="../../img/lookup.png" alt="搜索" />
                            </div>
                            <div class="cs-btn-handle-box" >
                                <el-button  type="primary" :disabled="cur_subject_id==null" @click="addContent">新增</el-button>
                            </div>
                        </div>
                    </div>

                    <div class="cs-list-table">
                        <!-- 列表 -->
                        <el-table :data="multipleSelection" border style="width: 100%" @row-click="rowClick" >
                            <el-table-column
                                    type="index"
                                    label="序号"
                                    width="65">
                            </el-table-column>
                            <el-table-column
                                    prop="title"
                                    label="事项名称"
                                    width="160" show-overflow-tooltip>
                            </el-table-column>
                            <el-table-column
                                    prop="statusName"
                                    label="发布状态">
                            </el-table-column>
                            <el-table-column
                                    prop="createName"
                                    label="发布人">
                            </el-table-column>

                            <el-table-column v-if="flag==5"
                                    label="最后一次更新时间">
                                   <!--  prop="updateTime" -->
                                <template scope="scope">
                                    <span style="margin-left: 10px">{{scope.row.updateTime|moment}}</span>
                                </template> 

                            </el-table-column>

                            <el-table-column
                                    prop="updaterName"
                                    label="更改人">
                            </el-table-column>
                            <el-table-column
                                    label="操作"
                                    width="180">
                                <template scope="scope">
                                    <el-button :disabled="scope.row.status != 1 && scope.row.status != 4 " @click.stop="releaseClick(scope.$index, scope.row)"  type="text"  size="small">发布</el-button>
                                    <el-button :disabled="scope.row.status != 3" v-on:click.stop="downClick(scope.$index, scope.row)"  type="text" size="small">撤下</el-button>
                                    <el-button v-on:click.stop="updateClick(scope.$index, scope.row)"  type="text" size="small">更新</el-button>
                                    <el-button @click.stop="deleteClick(scope.$index, scope.row)"  type="text" size="small">删除</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <!-- 分页 -->
                        <div class="cs-footer-page">
                            <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-sizes="[10, 20, 30, 50]"
                            :page-size="page_size"
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="records">
                            </el-pagination>
                        </div>
                    </div>
                </div>

                <div id="openinfo"  class="el-col el-col-24 el-col-xs-24 el-col-md-12 cs-col-right-box"  v-if="is_show_update">
                    <bsznupdatecomponent  ref="bsznupdatecomponent" :isClickrow="isClickrow" :mapHeight="mapHeight" :is_show_update="is_show_update" :selectRowData="selectRowData" @clearSelectRowData="clearSelectRowData" @hideUpdateModel= "hideUpdateModel" @getContentList="getContentList"></bsznupdatecomponent><!--更新 -->
                </div>
            </div>

        </div>

        <div v-if="is_show_catelist">
            <catecomponent ref="catecomponent" :mapHeight="mapHeight"  :is_show_catelist="is_show_catelist" :subject_items="subject_items" @hideCatedialog= "hideCatedialog" @changeCateList="changeCateList"></catecomponent><!--类别列表 -->
        </div>

    </div>

</template>




<script>


export default {

    data() {
        return {
            mapHeight:{
                type:Number,
                default:600
            },
            isClickrow:false,
            twoClass: 'el-col-md-12',
            onlyOneClass: 'el-col el-col-24 el-col-xs-24',
            mianStyle:{},
            status1:"待发布",
            status2:"未审批",
            status3:"已发布",
            status4:"已撤销",
            keyword:"",
            cur_subject_id:null,
            cur_subject_name:"",
            flag:6,
            is_show_update:false,
            is_show_catelist:false, //展示列表弹出
            selectRowData:{},
            tableData: [],
            currentPage:1,
            page_size:10,
            records:0,//数据总数
            multipleSelection: [],  //办事指南列表
            subject_items:[]  //办事指南类别列表
        }
    },
    components: {
        bsznupdatecomponent : function (resolve, reject) {
            require(['./update.vue'],  resolve)
        },
        catecomponent : function (resolve, reject) {
            require(['./catelist.vue'],  resolve)
        }
    },
    methods: {
        clearSelectRowData(flag) {
            this.is_show_update = false;
            this.selectRowData = {};
            this.filterTag(flag);
        },
        resetPage() {
            this.mapHeight = document.documentElement.offsetHeight  - 110;
        },
        showCatelist() {
            this.is_show_catelist = true;
        },
        hideCatedialog() {
            this.is_show_catelist = false;
        },
        changeCateList() {
            this.getCateList();
            //this.subject_items = items;
        },
        filterSubject(item) {
            this.cur_subject_id = item.id;
            this.cur_subject_name = item.name;
            this.getContentList();
        },
        filterTag(type) {
            this.flag = type;
            this.getContentList();
        },
        sousuo(){
            this.getContentList();

        },
        handleSizeChange(val) {
            this.page_size= val;
            this.getContentList();
            //console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getContentList();
            //console.log(`当前页: ${val}`);
        },
        getCateList() {
            let that = this;
            let token = global.data.getToken();
            if(!token) {
                return false;
            }
            this.$http.post(global.config.weburl + "api/sysSubject/querySysSubjectList.do",
                {token:token, page:that.currentPage,pageSize:100})
                .then(
                    function(res){
                        if(res.data.code==0){
                            var _data= res.data;
                            that.subject_items = _data.data;
                            if(that.subject_items[0]){
                                this.filterSubject(that.subject_items[0])
                            }
                        }else{
                            global.tools.handleError(res.data);
                        }

                    }
            );
        },
        getContentList() {
            let that = this;
            let token = global.data.getToken();
            if(!token) {
                return false;
            }
            if (!this.cur_subject_id){
                this.$message({
                    type: 'error',
                    message: "请先选择类别"
                });
                return false;
            }
            this.$http.post(global.config.weburl + "api/jwContent/queryJWContentList.do",
                    {token:token, sysSubjectId:that.cur_subject_id,status:that.flag, page:that.currentPage, pageSize:that.page_size, keyword:that.keyword, sortby:"update_time desc"})
            .then(
                function(res){
                    if(res.data.code==0){
                        var _data= res.data;
                        that.multipleSelection = _data.data;
                        console.log("updateTime")
                        console.log(that.multipleSelection)
                       // that.multipleSelection=_data.data.sort(by("updateTime"));
                        that.records = _data.pageInfo.records;
                    }else{
                        global.tools.handleError(res.data);
                    }

                    //console.log(res.data.data)
                    // this.tableData=res.data
                }
            );
        },
        addContent() {
            //添加办事指南
            //this.subject_items = {};
            this.is_show_update = true;
            this.selectRowData = {subjectId:this.cur_subject_id};
        },
        releaseClick(index,row) {   //发布办事指南
            let token = global.data.getToken();
            if(!token) {
                return false;
            }
            let that = this;
            that.$confirm('是否确认发布'+row.title+'?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((ref) => {
                if(ref){
                    this.$http.post(global.config.weburl + "api/jwContent/publishJWContent.do",{token:token,id:row.id})
                        .then(
                            function(res){
                                console.log(res.data.code);
                                if(res.data.code==0){
                                    that.$message({
                                        type: 'success',
                                        message: '发布成功!'
                                    });

                                    this.filterTag(5);
                                }else{
                                    global.tools.handleError(res.data);
                                }
                            }
                        );
                }
            }).catch(() => {
                //this.$message({
                //    type: 'info',
                //    message: '已取消发布'
                //});
            })
        },
        downClick(index,row) {   //撤下办事指南
            let token = global.data.getToken();
            if(!token) {
                return false;
            }
            let that = this;
            this.$confirm('是否确认撤下'+row.title+'?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((ref) => {
                if(ref){
                    //this.tableData.splice(index,1);
                    this.$http.post(global.config.weburl + "api/jwContent/removeJWContent.do",{token:token,id:row.id})
                        .then(
                                function(res){
                                    //console.log(res.data.code);
                                    if(res.data.code==0){
                                        this.$message({
                                            type: 'success',
                                            message: '撤下成功!'
                                        })
                                        this.filterTag(6);
                                    }else{
                                        global.tools.handleError(res.data);
                                    }
                                }
                        );

                }
            }).catch(() => {
                //this.$message({
                //    type: 'info',
                //    message: '已取消撤下'
                //});
            });
        },
        updateClick(index,row) { //更新
            //console.log(this.tableData[index].Matter)
            this.isClickrow=false;
            this.selectRowData = row;
            this.is_show_update = true;
            console.log(this.isClickrow)
        },
        rowClick(row){ //点击某行
            this.isClickrow=true;
            this.is_show_update=true;
            this.selectRowData = row;
        },
        deleteClick(index, row) {  //删除
            console.log(index, row);
            let that = this;
            let token = global.data.getToken();
            if(!token) {
                return false;
            }
            this.$confirm('是否确认删除'+row.title+'？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((ref) => {
                if (ref) {
                    this.$http.post(global.config.weburl + "api/jwContent/deleteJWContent.do",{token:token,id:row.id})
                        .then(
                            function(res){
                                if(res.data.code==0){
                                    this.$message({
                                        type: 'success',
                                        message: '删除办事指南成功!'
                                    })
                                    that.getContentList();
                                }else{
                                    global.tools.handleError(res.data);
                                }
                            }
                        );

                }
            }).catch(() => {
                //this.$message({
                //    type: 'info',
                //    message: '已取消删除'
                //});
            });
        },
        addListener () {
            const that = this;
            const Bus = global.eventHub;
        },
        hideUpdateModel() {
            console.log("隐藏更新");
            this.is_show_update = false;
            this.isClickrow=false;
        },
        removeListener () {
            const that = this;
            const Bus = global.eventHub;
        }
    },
    created: function(){
        let that = this;
        this.resetPage();
        this.mianStyle = {
            height:this.mapHeight +"px"
        }
        window.onresize = function(){
            that.resetPage();
        }
        this.getCateList();
        //this.addListener();
    },
    beforeDestroy: function () {
        //this.removeListener();
    }
}
var by = function(name){
 return function(o, p){
   var a, b;
   if (typeof o === "object" && typeof p === "object" && o && p) {
     a = o[name];
     b = p[name];
     if (a === b) {
       return 0;
     }
     if (typeof a === typeof b) {
       return a < b ? -1 : 1;
     }
     return typeof a < typeof b ? -1 : 1;
   }
   else {
     throw ("error");
   }
 }
}
</script>

<style>
.cs-btn-edit-subject{width: 36px;height: 36px;margin-left: 20px;}
.css-bszn-rek{line-height: 36px;margin-left: 10px;}
.cs-bszn-topcate .el-button{margin-top:5px;}

.cs-box-bszn-showcate{margin-top: 5px;}
</style>




